.surveyContainer {
    width: 100%;
    min-height: 100vh;
    background-color: var(--bg);
    text-align: left;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.surveyWrapper {
    padding: 30px 50px;
    display: flex;
    flex-direction: column;
    background-color: var(--white);
    width: 700px;
    border: 1px solid var(--border);
    border-radius: 10px;
}

.surveyWrapper small {
    text-align: left;
    margin-bottom: 15px;
}

.surveyWrapper h5 {
    margin-bottom: 20px;
}

.pBtnContainer {
    margin-top: 25px;
}

.pBtnContainer button {
    margin-left: 0px !important;
}

.backBtnContainer {
    position: absolute;
    bottom: 0;
    padding-bottom: 30px;

}