.informationContainer {
    width: 100%;
    min-height: 85vh;
    position: relative;
}



.eligibilityFormWrapper {
    background-color: var(--white);
    border: 1px solid var(--border);
    padding: 20px 20px 0px 20px;
    max-width: 800px;
    width: 100%;
    border-radius: 6px;

}

.eligibilityForm {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 100%;
}

.inputWrapper {
    width: 48%;
}