.collectTicketContainer {
    width: 100%;
    min-height: 100vh;
    background-color: var(--bg);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.collectTicketWrapper {
    max-width: 100%;
    min-width: 760px;
    padding: 20px 30px;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.collectTicketWrapper h2 {
    margin-bottom: 20px;
}

.collectTicketWrapper h6 {
    margin-bottom: 40px;
}

.collectTicketWrapper img {
    margin-bottom: 80px;
}

.collectTicketWrapper h5 {
    margin-bottom: 15px;
}

.btnWrapper {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    width: 330px;
}

#verifyBtn {
    border: 1px solid var(--primary);
    color: var(--primary) !important;
}

#verifyBtn:hover {
    background-color: var(--primary) !important;
    color: var(--white) !important;
    border: 1px solid var(--primary) !important;
}